/*Color modifying like light orange */

.cart-page * {
    color: #444;
}

.cart-page {
    max-width: 800px;
    margin: 0 auto 10rem auto;
    padding: 20px;
}

.cart-page ul {
    list-style-type: none;
    padding: 0;
}

.cart-page li {
    border: 1px solid #ddd;
    border-radius: 5px;
    margin-bottom: 20px;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.cart-item img {
    width: 80px;
    height: 80px;
    object-fit: cover;
    border-radius: 5px;
    margin-right: 20px;
}

.cart-item-details {
    flex-grow: 1;
}

.cart-item-details h2 {
    font-size: 1.2em;
    margin: 0;
    color: #333;
}

.cart-item-details p {
    font-size: 0.9em;
    color: #666;
}

.cart-item-details span {
    font-size: 1.1em;
    font-weight: bold;
    color: #444;
}

.quantity-controls {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-left: auto;
}

.quantity-controls button {
    background-color: #f68b1e;
    color: white;
    border: none;
    border-radius: 3px;
    padding: 5px 10px;
    cursor: pointer;
    font-size: 1.2em;
}

.quantity-controls button:hover {
    background-color: #93500d;
}

.checkout-button {
    background-color: #f08c42;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 1.2em;
    cursor: pointer;
    transition: background-color 0.3s ease;
    display: block;
    margin: 20px auto 0;
    width: 99%;
}

.checkout-button:hover {
    background-color: #93500d;
}

.response-message {
    color: #f68b1e;
    font-size: 25px;
    font-weight: 700;
    text-align: center;
}

.cart-page p {
    text-align: center;
    font-size: 1.2em;
    color: #666;
}
