/*Full Map Component Matches With Address Page*/
.address-page {
    position: relative;
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: rgba(249, 249, 249, 0.9);
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    z-index: 1;
}

.address-page h2 {
    text-align: center;
    margin-bottom: 20px;
    font-size: 24px;
    color: #1a3d6d;
}

.address-page form {
    display: flex;
    flex-direction: column;
}

.address-page label {
    margin-bottom: 10px;
    font-weight: bold;
}

.address-page input {
    padding: 8px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    width: 100%;
}

.address-page button {
    padding: 10px 20px;
    background-color: #f08c42;
    color: white;
    border-radius: 4px;
    border: none;
    cursor: pointer;
    font-size: 16px;
}

.address-page button:hover {
    background-color: #8c4d0e;
}

.error-message {
    color: red;
    margin-bottom: 20px;
    text-align: center;
}
